@font-face {
  font-family: "Gilroy";
  src: url("../resources/fonts/Gilroy-ExtraBold.woff2");
  font-display: swap;
  font-weight: 800;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../resources/fonts/Montserrat-Medium.woff2");
  font-display: swap;
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../resources/fonts/Montserrat-Regular.woff2");
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

body {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

h1 {
  margin: 0;
  padding: 0;
}

h2 {
  margin: 0;
  padding: 0;
}

h3 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.btn {
  position: relative;
  padding: 9px 47px;
  font: 800 14px/20px "Gilroy", sans-serif;
  border: 1px solid rgba(34, 34, 34, 0.3);
  color: #222222;
  transition: border-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.btn:hover {
  border-color: #222222;
  transition: border-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.btn:active {
  border-color: #656565;
  color: #656565;
  transition: border-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.btn-arrow {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  padding-left: 5px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.3);
  background-position-x: 60%;
}
.btn-arrow:hover {
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
  transition: border-top-color 1.1s ease, border-right-color 0.5s ease 0s, border-bottom-color 1s ease 0s, border-left-color 1.2s ease 0s;
  transform: rotate(border(45deg));
}
.btn-arrow:hover .arrow-icon {
  fill: #222222;
  transition: fill 0.5s ease-in-out;
}
.btn-arrow::before, .btn-arrow::after {
  content: "";
  position: absolute;
  top: -1px;
  right: -1px;
  bottom: -1px;
  left: -1px;
  box-sizing: inherit;
  width: 40px;
  height: 40px;
  border-radius: 100%;
}
.btn-arrow::before {
  border: 1px solid transparent;
}
.btn-arrow:hover::before {
  border-top-color: #222222;
  border-right-color: #222222;
  border-bottom-color: #222222;
  transform: rotate(45deg);
  transition: border-top-color 0.35s linear, border-right-color 0.35s linear 0.3s, border-bottom-color 0.35s linear 0.4s;
}
.btn-arrow::after {
  border: 0 solid #222222;
}
.btn-arrow:hover::after {
  border-top: 1px solid #222222;
  border-left-width: 1px;
  border-right-width: 1px;
  transform: rotate(270deg);
  transition: transform 0.9s linear 0s, border-left-width 0s linear 0.55s;
}

.input-reset {
  width: 100%;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 170px;
  max-width: 1440px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(4, 270px);
  gap: 10px;
}

.text-stroke {
  -webkit-text-stroke: 1.75px #222222;
  color: #FFFFFF;
}

.btn-file:hover .btn-file--icon {
  fill: #C6876D;
}

.pic {
  display: block;
  height: 100%;
}

@media (max-width: 1024px) {
  .container {
    margin: 0 auto;
    padding: 0 100px;
    max-width: 1024px;
  }

  .btn {
    font-size: 12px;
  }
}
@media (max-width: 768px) {
  .container {
    margin: 0 auto;
    padding: 0 50px;
    max-width: 768px;
  }

  .grid {
    grid-template-rows: repeat(4, 170px);
    gap: 5px;
  }

  .btn {
    font-family: "Montserrat";
    font-weight: 600;
    font-size: 10px;
  }

  .btn-arrow {
    padding-left: 3px;
  }
  .btn-arrow::before, .btn-arrow::after {
    width: 30px;
    height: 30px;
  }
}
