.header__logo:hover .logo {
  fill: #C6876D;
  transition: fill 0.2s ease-in-out;
}

.nav__link:hover {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}
.nav__link:hover::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -35px;
  width: 100%;
  height: 1px;
  background-color: #222222;
  z-index: 2;
}

.nav__link--reverse:hover::after {
  background-color: #FFFFFF;
}

.header__marker:hover .marker-icon {
  fill: #C6876D;
  transition: fill 0.3s ease-in-out;
}

@keyframes dropDown {
  from {
    transform: translateY(-100px);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes backUp {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-100px);
  }
}
.hero__callback-link--reverse:hover {
  border-color: #FFFFFF;
  transition: border-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.hero__callback-link--reverse:active {
  border-color: #656565;
  transition: border-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.hero__callback-link--reverse:active .btn-streak--reverse {
  background-color: #656565;
  transition: background-color 0.3s ease-in-out;
}

.hero__services-btn:hover {
  color: #C6876D;
  transition: color 0.3s ease-in-out;
}
.hero__services-btn:active {
  color: #6D5247;
  transition: color 0.3s ease-in-out;
}

.hero__services-link:hover {
  color: #C6876D;
  transition: color 0.3s ease-in-out;
}
.hero__services-link:active {
  color: #6D5247;
  transition: color 0.3s ease-in-out;
