html {
  box-sizing: border-box;
}

body {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

h1 {
  margin: 0;
  padding: 0;
}

h2 {
  margin: 0;
  padding: 0;
}

h3 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.btn {
  position: relative;
  padding: 9px 47px;
  font: $font-title-mini;
  border: 1px solid $color-border-btn;
  color: $color-mine-shaft;
  transition: border-color 0.3s ease-in-out, color 0.3s ease-in-out;

  &:hover {
    border-color: $color-mine-shaft;
    transition: border-color 0.3s ease-in-out, color 0.3s ease-in-out;
  }

  &:active {
    border-color: $color-active;
    color: $color-active;
    transition: border-color 0.3s ease-in-out, color 0.3s ease-in-out;
  }
}

.btn-arrow {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  padding-left: 5px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid $color-services-border;
  background-position-x: 60%;

  &:hover {
    border-top-color: transparent;
    border-right-color: transparent;
    border-bottom-color: transparent;
    transition:
      border-top-color 1.1s ease,
      border-right-color 0.5s ease 0s,
      border-bottom-color 1s ease 0s,
      border-left-color 1.2s ease 0s;
    transform: rotate(border(45deg));

    .arrow-icon {
      fill: $color-mine-shaft;
      transition: fill 0.5s ease-in-out;
    }
  }

  &::before,
  &::after {
    content: '';
    position: absolute;
    top: -1px;
    right: -1px;
    bottom: -1px;
    left: -1px;
    box-sizing: inherit;
    width: 40px;
    height: 40px;
    border-radius: 100%;
  }

  &::before {
    border: 1px solid transparent;
  }

  &:hover::before {
    border-top-color: $color-mine-shaft;
    border-right-color: $color-mine-shaft;
    border-bottom-color: $color-mine-shaft;
    transform: rotate(45deg);
    transition:
      border-top-color 0.35s linear,
      border-right-color 0.35s linear 0.30s,
      border-bottom-color 0.35s linear 0.40s;
  }

  &::after {
    border: 0 solid $color-mine-shaft;
  }

  &:hover::after {
    border-top: 1px solid $color-mine-shaft;
    border-left-width: 1px;
    border-right-width: 1px;
    transform: rotate(270deg);
    transition: transform 0.9s linear 0s,
      border-left-width 0s linear 0.55s;
  }
}

.input-reset {
  width: 100%;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 170px;
  max-width: 1440px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(4, 270px);
  gap: 10px;
}

.text-stroke {
  -webkit-text-stroke: 1.75px $color-mine-shaft;
  color: $color-white;
}

.btn-file {
  &:hover {
    .btn-file--icon {
      fill: $color-hover;
    }
  }
}

.pic {
  display: block;
  height: 100%;
}

@media (max-width:1024px) {
  .container {
    margin: 0 auto;
    padding: 0 100px;
    max-width: 1024px;
  }

  .btn {
    font-size: 12px;
  }
}

@media (max-width:768px) {
  .container {
    margin: 0 auto;
    padding: 0 50px;
    max-width: 768px;
  }

  .grid {
    grid-template-rows: repeat(4, 170px);
    gap: 5px;
  }

  .btn {
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 10px;
  }

  .btn-arrow {
    padding-left: 3px;

    &::before,
    &::after {
      width: 30px;
      height: 30px;
    }
  }

}